import React, { memo } from "react";
import numeral from 'numeral';
import { scaleLinear } from 'd3-scale';
import {
  ZoomableGroup,
  ComposableMap,
  Geographies,
  Geography,
  Sphere,
  Marker
} from "react-simple-maps";

const geoUrl =
  "https://raw.githubusercontent.com/zcreativelabs/react-simple-maps/master/topojson-maps/world-110m.json";

const WorldMap = ({ setTooltipContent, countries }) => {

  const colorScale = scaleLinear()
    .domain([1000, 10000, 100000, 1000000, 10000000, 100000000])
    .range(["rgb(149,220,244)", "rgb(84,203,242)", "rgb(0,172,227)", "rgb(0,142,188)", "rgb(0,112,146)", "rgb(0,80,107)"]);

  const paises = iso => {
    for (var i = 0; i < countries.length; i++) {

      if (countries[i].countryInfo.iso2 == iso) {

        return numeral(countries[i].cases).format("0,0");
      }
    }
    return "Sin datos";
  }

  const color = iso => {
    console.log("iso_A2" + iso);
    for (var i = 0; i < countries.length; i++) {
      if (countries[i].countryInfo.iso2 == iso) {
        console.log("iso_A2: " + iso + ", " + countries[i].countryInfo.iso2);
        console.log(countries[i].cases)
        return countries[i].cases;
      }
    }
  }

  const handleClick = (text) => {
    console.log("NAME: " + text)
    localStorage.setItem("NAME", text)
    // route to new page by changing window.location
    window.open('/country', "_self") //to open new page
  }

  return (

    <ComposableMap data-tip="" projectionConfig={{ scale: 200 }}>
      {countries.length > 0 && (
        <Geographies geography={geoUrl}>
          {({ geographies }) =>
            geographies.map((geo) => {
              const d = color(geo.properties.ISO_A2)
              return (
                <Geography
                  key={geo.rsmKey}
                  geography={geo}
                  fill={d ? colorScale(d) : "rgb(122, 122, 122)"}
                  onMouseEnter={() => {
                    const { NAME, ISO_A2 } = geo.properties;
                    setTooltipContent(`${NAME} — Contagios: ${paises(ISO_A2)}`);
                  }}
                  onMouseLeave={() => {
                    setTooltipContent("");
                  }}
                  style={{
                    hover: {
                      fill: "#1fb299",
                      outline: "none"
                    },
                    pressed: {
                      fill: "#bcf5ec",
                      outline: "none"
                    }
                  }}
                />
              );
            })
          }
        </Geographies>
      )}

    </ComposableMap>

  );
}
export default memo(WorldMap);
